.About {
    height: 700px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #020617;
}

.About-inner {
    height: 700px;
    width: 1100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.about-image {
    position: relative;
    min-width: 500px;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.behind {
    position: absolute;
    z-index: 1;
    left: -30px;
    top: 30px;
}

.about-image-item {
    width: 475px;
    border-radius: 24px;
    z-index: 2;
}

.about-stuff {
    width: 500px;
    height: 400px;
    display: flex;
    text-align: start;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.about-text {
    width: 461px;
    /* height: 81px; */
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #6B7280;
    margin-top: 30px;
}

.about-header {
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    color: #F3F4F6;
}

.about-button {
    margin-top: 30px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 170px;
    min-height: 43px;
    background: rgb(42, 42, 194);
    border-radius: 4px;
    /* font */
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #ffffff;
    transition: all .1s;
    border: none;
}

.about-button:hover {
    cursor: pointer;
    background: rgb(25, 25, 105);
    color: #F3F4F6;
}

.about-button:active {
    cursor: pointer;
    background: rgb(41, 41, 146);
    color: #F3F4F6;
}

.highlight1 {
    color: rgb(161, 157, 157);
}