.Experience {
    width: 100%;
    height: 750px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Experience-inner {
    width: 1100px;
    height: 580px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.experience-header {
    width: 460px;
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    color: #F3F4F6;
}

.experience-up-text {
    width: 460px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #6B7280;
}

.experience-up {
    width: 100%;
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}

.experience-down {
    display: flex;
    width: 1100px;
    height: 350px;
    justify-content: space-between;
}

.experience-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 342px;
    background: #020617;
    border-radius: 12px;
}

.experience-item-inner {
    width: 300px;
    height: 240px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    text-align: start;
}

.experience-date {
    width: 167px;
    height: 27px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #D1D5DB;
}

.experience-name {
    margin-top: 15px;
    width: 231px;
    height: 41px;
    font-style: normal;
    font-weight: 700;
    font-size: 26px;
    line-height: 41px;
    text-align: center;
    color: #EAB308;
    display: flex;
    justify-content: start;
    align-items: start;
}

.experience-text {
    margin-top: 40px;
    width: 286px;
    height: 108px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #6B7280;
}