.BurgerMenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    overflow: hidden;
    background-color: #000000;
    color: white;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: start;
    border-bottom: 3px solid rgb(59, 58, 58);
}

.BurgerMenu-inner {
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
}

body.menu-open {
    overflow: hidden;
}

.App {
    overflow-y: auto;
    overflow-x: hidden;
}

.exit-button {
    position: absolute;
    right: 40px;
    top: 15px;
}

.navigation1 {
    letter-spacing: 3px;
    max-height: 390px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
}

.navigation-item1 {
    text-transform: uppercase;
    font-family: 'Lexend Deca';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    color: #9CA3AF;
    transition: all .1s;
    background: none;
    border: none;
    margin-top: 0px;
    margin-bottom: 25px;
}

.navigation-item1:hover {
    color: #ffffff;
    cursor: pointer;
}

.navigation-item1:active {
    color: #b1aeae;
    cursor: pointer;
}

.header-btn {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 50px;
    min-height: 40px;
    background: rgb(184, 184, 212);
    border-radius: 4px;
    /* font */
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #ffffff;
    border: none;
    transition: all .1s;
}

.header-btn:hover {
    cursor: pointer;
    background: rgb(25, 25, 105);
    color: #F3F4F6;
}

.header-btn:active {
    cursor: pointer;
    background: rgb(41, 41, 146);
    color: #F3F4F6;
}

.burger {
    width: 40px;
}