.Portfolio {
    width: 100%;
    height: 2200px;
    display: flex;
    justify-content: center;
    align-items: start;
    background-color: #020617;
}

.Portfolio-inner {
    width: 1100px;
    height: 2200px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.portfolio-down {
    width: 100%;
    height: 1950px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-wrap: wrap;
}

.portfolio-header {
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    /* identical to box height, or 152% */
    text-align: center;
    color: #F3F4F6;
    width: 262px;
    height: 64px;
}

.portfolio-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    /* or 169% */
    text-align: center;
    color: #9CA3AF;
    width: 461px;
    height: 81px;
}

.portfolio-up {
    margin-top: 80px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 180px;
}

.highlight {
    color: #EAB308;
}

.item {
    width: 420px;
    height: 400px;
    background: linear-gradient(0deg, #323443, #323443), #EFF6FF;
    /* background: #111827; */
    border-radius: 20px;
    border: 2px solid rgb(43, 42, 42);
    box-shadow: rgba(255, 255, 255, 0.16) 10px 5px 30px 2px, rgba(0, 0, 0, 0.23) 20px 23px 26px;
}

.item-img1 {
    background-image: url(../../assets/game1.jpg);
