.Links {
    height: 325px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #020617;
}

.Links-inner {
    width: 700px;
    height: 325px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    text-align: center;
}

.socials-item {
    width: 45px;
    transition: all .2s;
}

.socials-item:hover {
    cursor: pointer;
}

.links-header {
    margin-top: 50px;
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    color: #F3F4F6;
}

.links-text {
    width: 600px;
    /* height: 81px; */
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #6B7280;
    margin-top: 30px;
}

.socials {
    margin-top: 40px;
    width: 285px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}