.Header {
    position: fixed;
    background-color: #020617;
    width: 100%;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 4;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Header-inner {
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.burger {
    width: 40px;
}

.exit {
    width: 30px;
}

.navigation {
    width: 625px;
    display: flex;
    justify-content: space-between;
}

.navigation-item {
    text-transform: uppercase;
    font-family: 'Lexend Deca';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #9CA3AF;
    transition: all .1s;
    background: none;
    border: none;
}

.logo-item {
    cursor: pointer;
}

.navigation-item:hover {
    color: #ffffff;
    cursor: pointer;
}

.navigation-item:active {
    color: #b1aeae;
    cursor: pointer;
}

.burger-button {
    position: absolute;
    right: 40px;
}