.Contact {
    width: 100%;
    height: 750px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #020617;
}

.Contact-inner {
    width: 1100px;
    height: 580px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.contact-up {
    width: 100%;
    height: 180px;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.contact-header {
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    /* identical to box height, or 152% */
    text-align: center;
    color: #F3F4F6;
    width: 262px;
    height: 64px;
}

.contact-up-text {
    margin-top: 20px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    /* or 169% */
    text-align: center;
    color: #9CA3AF;
    width: 650px;
    height: 81px;
}


/* ///////////// */

.contact-button {
    margin-top: 30px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 170px;
    min-height: 43px;
    background: rgb(42, 42, 194);
    border-radius: 4px;
    /* font */
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #ffffff;
    transition: all .1s;
    border: none;
    border-radius: 8px;
    outline: none;
}

.contact-button:hover {
    cursor: pointer;
    background: rgb(25, 25, 105);
    color: #F3F4F6;
}

.contact-button:active {
    cursor: pointer;
    background: rgb(41, 41, 146);
    color: #F3F4F6;
}

.contact-down {
    height: 500px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.contact-down-inner {
    width: 100%;
    height: 350px;
    display: flex;
}

.options {
    width: 347px;
    height: 82px;
    font-family: 'Lexend Deca';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    /* or 256% */
    color: #ffffff;
}

.highlight2 {
    color: #EAB308;
    font-weight: 700;
    margin-right: 10px;
}

.contact-down-left {
    width: 350px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

form {
    width: 550px;
    height: 400px;
}

.contact-down-right {
    width: 550px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.option {
    width: 350px;
    height: 63px;
    background-color: #1F2937;
    border: none;
    border-radius: 8px;
    outline: none;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    color: #F3F4F6;
    padding-left: 10px;
    margin-bottom: 30px;
}

.option::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    color: #5f6061;
    padding-left: 10px;
}

.option:focus {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    color: #F3F4F6;
    padding-left: 10px;
}

.option:valid {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    color: #F3F4F6;
    padding-left: 10px;
    background-color: #1F2937;
}

.contact-text {
    width: 540px;
    height: 106px;
    background-color: #1F2937;
    border: none;
    border-radius: 8px;
    outline: none;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #F3F4F6;
    padding-left: 20px;
    text-align: left;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.contact-text ::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 41px;
    color: #5f6061;
    padding-left: 10px;
}

.contact-text:focus {}

.contact-text:valid {}